<template>
    <div>
    <!-- 里边是需要放input组件的所以要留出卡槽 -->
    <label>{{label}}</label>
    <slot></slot>
    <p class="errmsg" if="iserror">{{errormessage}}</p>
    </div>
</template>
<script>
import Schema from "async-validator"

export default {
    inject:["formthis"],
    props:{
        label:{
            type:String,
            required:true
        },
        prop:{
            type:String,
            required:true
        }
    },
    data(){
        return{
            errormessage:"",
            iserror:false
        }
    },
    mounted(){
        this.$on("validateinput",this.validator);
    },
    methods:{
        validator(){
        //验证需要验证的规则和具体要验证的
           let rule=this.formthis.rules[this.prop];
           let v=this.formthis.model[this.prop];
           let schema=new Schema({[this.prop]:rule});
           schema.validate({[this.prop]:v},errors=>{
               if(errors){
                  this.iserror=true;
                  this.errormessage=errors[0].message;
               }else{
                   this.iserror=false;
                   this.errormessage="";
               }
           });
            
        }
    }
}
</script>

<style scoped>
    .errmsg{
        color:red;
        font-size:12px;
    }
</style>