<template>
    <span>
        <input :value="value" :type="type" @input="fn" />
    </span>
</template>
<script>
export default {
    props:{
        value:{
            type:String,
            required:true
        },
        type:{
            type:String,
            required:true
        }
    },
    data(){
        return {

        }
    },
    methods:{
        fn(e){
            let value=e.target.value;
            this.$emit("input",value);
            this.$parent.$emit("validateinput");
        }
    }
}
</script>